.ubc-background-image-controll__settings {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px; }
  .ubc-background-image-controll__settings > div.components-base-control {
    -webkit-box-flex: 0;
            flex: 0 0 50%;
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 0 !important;
    margin-top: 5px; }

.ubc-background-image-controll__preview-top {
  -webkit-box-flex: 0;
          flex: 0 0 100%; }

.ubc-background-image-controll__upload {
  padding: 0;
  border: 0; }

.ubc-background-image-controll__preview-bottom {
